import React, { useState } from "react"
import Img from "gatsby-image"
import { AnimateSharedLayout, motion as m } from "framer-motion"

import OverlayImage from "./overlay"

const Featured = ({ content, sectionId, sectionTitle }) => {
  const [selected, setSelected] = useState(null)

  return (
    <section className="cmp-featured" id={sectionId}>
      <div className="cmp-featured__container">
        <h3 className="cmp-featured__title">{sectionTitle}</h3>
        <AnimateSharedLayout type="crossfade">
          <div className="cmp-featured__images">
            {content.map(({ node }) => (
              <m.div
                className="cmp-featured__image"
                key={node.id}
                layoutId={node.id}
                onClick={() => !selected && setSelected(node)}
              >
                <Img
                  fluid={node.frontmatter.hero?.childImageSharp?.fluid}
                  alt={node.frontmatter.title}
                />
              </m.div>
            ))}
          </div>
          <OverlayImage selected={selected} setSelected={setSelected} />
        </AnimateSharedLayout>
      </div>
    </section>
  )
}

export default Featured
