import React from "react"
import { graphql } from "gatsby"

import SEO from "../components/seo"

import Layout from "../components/layout/layout"
import Hero from "../components/home/hero"
import Featured from "../components/home/featured"
import About from "../components/home/about"
import Tech from "../components/home/tech"

const IndexPage = ({ data }) => {
  const getContent = tag =>
    data.allMarkdownRemark.edges.filter(({ node }) =>
      node.frontmatter.tags.includes(tag)
    )

  return (
    <Layout>
      <SEO />
      <Hero />
      <Featured
        content={getContent("data-viz")}
        sectionId={"data-viz"}
        sectionTitle={"Data Visualization"}
      />
      <Featured
        content={getContent("data-science")}
        sectionId={"data-science"}
        sectionTitle={"Data Science"}
      />
      <About />
      <Tech />
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
  query Featured {
    allMarkdownRemark(sort: { fields: frontmatter___date, order: DESC }) {
      edges {
        node {
          id
          frontmatter {
            title
            tags
            description
            socials {
              type
              url
            }
            hero {
              childImageSharp {
                fluid(maxWidth: 500) {
                  ...GatsbyImageSharpFluid
                  aspectRatio
                }
              }
            }
          }
        }
      }
    }
  }
`
