import React from "react"
import Img from "gatsby-image"
import { useStaticQuery, graphql } from "gatsby"

const About = () => {
  const meImg = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "me.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <section className="cmp-about">
      <div className="cmp-about__container">
        <div className="cmp-about__imagebox">
          <Img
            alt="me"
            className="cmp-about__image"
            fluid={meImg.placeholderImage.childImageSharp.fluid}
          />
        </div>
        <div className="cmp-about__content">
          <p>
            I graduated from college as a Mechatronics Engineer in 2017. At that
            time I found out about Data Science, and I was attracted by its mix
            between science and communication.
          </p>
          <p>
            I started working in Tech as a Developer where I got to work my
            coding skills while learning about the Data Science Methodology,
            aiming what I learned towards analytic solutions.
          </p>
          <p>
            Then in 2020, looking for proper ways to visually communicate
            insights I discovered D3 and it opened the door to a world of
            visuals and design, and I was touched by that.
          </p>
          <p>
            Together the process of exploring and transforming data and
            designing visual ways to convey its underlying relationships make up
            the place from where I want to communicate.
          </p>
          <p>
            While working as a Developer I've gathered a range of tools to work
            with data and visualizations. These are some of my favorite ones.
          </p>
        </div>
      </div>
    </section>
  )
}

export default About
